<template>
  <div class="NavMenu">
    <el-card body-style="width: 100%;height: 100%;padding: 0;">
      <el-card>
        <el-menu router default-active="2" class="el-menu-vertical-demo"  :unique-opened="true">
          <template v-for="(item,index) in menus">
            <div :index="index + ''"  :key="index" v-if="!item.hidden">
              <template v-for="(item,index) in item.children">
                <el-submenu :index="index + ''"  :key="index" v-if="!item.hidden">
                  <template slot="title">
                    <i :class="item.iconClass" style=" padding-right: 10px; padding-left: 5px;"></i>
                    <span>{{item.name}}</span>
                  </template>
                  <el-menu-item-group  v-for="(child,index) in item.children" :key="index">
                    <el-menu-item :index="child.path">
                      <i :class="child.iconClass"></i>
                      {{child.name}}
                    </el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
              </template>
            </div>
          </template>
          <el-menu-item>
            <i class="el-icon-setting"></i>
            <span slot="title" style="padding-left: 0;" @click="qx()">权限管理</span>
          </el-menu-item>
        </el-menu>
      </el-card>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menus:[]
    };
  },
  created() {
    this.menus=[...this.$router.options.routes]
  },
  methods: {
    qx(){
      this.$message({message:'无操作权限',type:'warning'})
    },

  }
}
</script>

<style lang="scss">
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 95%;
  min-height: 100%;
}
.NavMenu {
  width: 100%;
  height: 100%;
  .el-card{
    width: 100%;
    height: 100%;
    .el-card__body{
      padding: 0;
      .el-card{
        width: 90%;
        height: auto;
        margin: 10px auto;
        .el-card__body{
          .el-menu{
            width: 100%;
            height: 100%;
            .el-submenu{
              .el-menu-item-group{
                .el-menu-item{
                  padding: 0 0 0 45px;
                }
              }
            }
          }
        }
      }
    }
  }
}
.el-menu-vertical-demo{
  border-right-width: 0;
}
.el-aside {
  overflow: auto;
  box-sizing: border-box;
  flex-shrink: 0;
  height: 100%;
  padding: 20px;
}

//取消悬停颜色
.el-submenu__title:hover, .el-menu-item:focus, .el-menu-item:hover{
  background-color: rgba(255, 255, 255, 0.97);
  color: #409EFF;
}

</style>
